.outer{
    /* margin: auto; */
}
.Navbar{
    display: flex;
    height: 50px;
    align-items: center;
    background-color: black;
    width: 100%;
    justify-content: space-between;
    font-size: 1rem;
    /* padding-left: 10px; */
    /* padding-right: 10px; */
}
.avatar {
    background-color: yellow;
    border-radius: 50%;
    object-fit: contain;
    overflow: hidden;
    
    
}
.avatar img {
    align-items: center;
    border-radius: 50%;
    width: 30px;
}

.useroptions{
    color: white;
    display: flex;
    flex-direction: column;
    /* gap: 5px; */
    margin: 2px;
}
.cartoption{
    display: flex;
    color: white;
    align-items: center;
    margin-right: 10px;
}

.amazonlogo {
    min-width: 60px;
    
    margin-left: 10px;
    max-height: 30px;
    min-height: 20px;
    object-fit: cover;
}
.searchbar{
    display: flex;
    align-items: center;
    height: 50px;
    min-width: 0vw;
    flex:0.92;
    /* max-width:70%; */

}
@media only screen and (max-width: 700px) {
    .Searchbar {
      width: 5vw;
    }
    .searchbar{
        flex: 0.7;
    }
  }
@media only screen and (max-width: 400px) {
    .Searchbar {
     display: none;
    }
    .searchbar{
        display: flex;
        justify-content: center;
    }

  }
.upperpart{
    color: rgb(203, 205, 207);
    font-size: 0.7rem;
}
.Searchbar{
    height: 24px;
    flex: 1;
    font-size: 1rem;
    
    outline: 0;

}
.lowerpart{
    color: aliceblue;
    font-size: 0.9rem;
    
}

.cartlogo{
    max-height: 30px;
    object-fit: cover;
}
.serchlogo{
    max-height: 30px;
    object-fit: cover;
}


.Filter{
    position: absolute;
  background-color: #f9f9f9;
  /* box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2); */
  z-index: 1000;
  margin: auto;
  width: 70vw;
  left: 7%;
  font-size: 1rem;
  overflow: hidden;
  gap: 10px;
  /* margin-top: 10px; */
}
.Filter:hover , .serchlogo:hover{
    cursor: pointer;
}
.Filter div{
    width: 100%;
    /* text-align: center; */
    margin: auto;
    margin-top: 5px;
}
.searchitem{
    /* border: 1px solid black; */
    padding: 5px;
}
.searchitem:hover{
    background-color: #b4adad;
}