.Cart{
    margin: 10px;
    display: flex;
    gap:20px

}
.Leftside{
    width: 60%;
    
}
.checkbox{
    
}
.check{
    object-fit: contain;
    display: flex;
    align-items: center;
}
.Rightside{
    display: flex;
    flex-direction: column;
    border: 2px solid green;
    padding: 1%;
    max-height: 30vh;
    width: 35%;
    gap: 20px;
    background-color: rgba(229, 230, 231, 0.911);

}
.Pay:hover{
    cursor: pointer;
    box-shadow: -3px 18px 20px 0px rgba(99,89,99,1);
    
}
.emptycart{
    width: 100%;
    margin-top: 10%;

}

.Pay{
    border-color: aliceblue;
    outline: none;
    box-shadow: -3px 6px 5px 0px rgba(176,164,176,1);
  transition: all .3s ease-in-out;
    
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    margin-top: 10px;
    height: 10vh;
    margin: auto;
    
    width: 60%;
    font-size: large;
    color: white;
    background-color:   #ce9c1f;
    /* margin: auto; */
    text-align: center;


}
