.Amazonlogo{
    display: flex;
    text-align: center;
    justify-content: center;
}
.amazonimage{
    width: 60%;
    height: 100%;

}
.Loginpage{
    width: 500px;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
@media only screen and (max-width: 500px) {
    .Loginpage {
      width:100vw;
    }
  }
.textemail{
    height: 30px;
    width: 95%;
}
.confirm{
    height: 30px;
    width: 100%;
    background-color: #ce9c1f;
    outline: none;
    border-color: aliceblue;
    /* font-weight: 500; */
    padding: 5px;
    font-size: 15px;

}
.Create{
    height: 30px;
    width: 100%;
    background-color: rgb(189, 189, 189);
    outline: none;
    border-color: aliceblue;
}
.Confirm{
    
    margin-top: 10px;
    max-width:95%;
}
.terms{
    max-width:95%;
    font-weight: 500;
}
.Container{
    gap: 10px;
    border: 2px solid rgb(158, 160, 158);
    display: flex;
    width: 60%;
    flex-direction: column;
    padding: 20px;
    /* justify-content: center; */
}