*{
    padding:0;
    margin: 0;

}
.Product{
    box-shadow: -3px 6px 5px 0px rgba(176,164,176,1);
  /* transition: all .3s ease-in-out; */
  position: relative;
  height: 100%;
  object-fit: fill;
    /* z-index:10; */
    display: flex;
    flex-direction: column;
    align-items:center;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: rgba(245, 243, 238, 0.849);
}
.Product:hover{
    box-shadow: -3px 18px 20px 0px rgba(99,89,99,1);
}
.title ,.price ,.stars{
    text-align: center;
}
.productimg{
    object-fit: fill;
    max-width: 60%;
    z-index: 10;
    margin: auto;
}
