.Productimg{
    
    object-fit: cover;
    display: flex;
    height: 80%;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
}
.specfication{
    margin-bottom: 10px;
    /* border: 2px solid green; */
}
.Star{
    width: 30px;
}
.Add{
    margin: auto;
    box-shadow: -3px 6px 5px 0px rgba(176,164,176,1);
  transition: all .3s ease-in-out;
    height: 50px;
    width: 20vw;
    border-color: rgba(212, 203, 203, 0.849);
    outline: none;
    /* margin-bottom: 10px; */
    background-color:  #ce9c1f;
    color: rgb(248, 248, 250);
}
.Add:hover{
    box-shadow: -3px 18px 20px 0px rgb(82, 82, 78);
}