.Sign{
    padding-top: 30px;
    padding-bottom: 30px;
    display: flex;
    flex-direction: column;
    width: 50%;
    justify-content: center;
    text-align: center;

    
    align-items: center;
    margin: auto;
}
.Signin{
    width: 40%;
    height: 2rem;
    border-radius: 5px;
    outline: 0ch;
    border-color: aliceblue;
    background-color: #ce9c1f;
}
.new{
    font-size: small;
}
.new a{
    text-decoration: none;
}
.new a:hover{
    cursor: pointer;
    color: #e7ab14;
}
.Signin:hover{
    cursor: pointer;
}