*{
    margin: 0;
    padding: 0;
}
.HomeImage{
    width: 100%;
    max-height: 80vh;
    /* object-fit: fill; */
    /* width: 100%; */

}
.frontpage {
    max-width: 95%; 
    margin: auto;
  }
  
  .frontpage::after {
    display: block;
    position: relative;
    background-image: linear-gradient(to bottom, rgba(36, 31, 31, 0) 0, #a09c9c 100%);
    margin-top: -150px;
    height: 150px;
    width: 100%;
    content: '';
  }
.frontpage{
    z-index: 10;
    max-width: 95%;
    margin: auto;
}
.AllProd{
    position: relative;
    bottom: 150px;
    display: grid;
  grid-template-columns: repeat(auto-fill, minmax(30%, 1fr));
  gap: 10px;
  grid-auto-flow: dense;
  align-items: center;
  justify-content: baseline;
  margin: auto;
}
@media only screen and (max-width: 700px) {
    .AllProd {
        grid-template-columns: repeat(auto-fill, minmax(40%, 1fr));
    }
  }
.Homepage{
    background-color: rgb(228, 229, 231);
    width: 100%;
    height: auto;
}
.Listproduct{
    display: flex;
    flex-direction: column;
}
.lists{
     width: 100%;
     height: 100%;
    margin: auto;
    z-index: 100;
    object-fit: fill;
}